<template>
  <div class="container">
    <div class="create-post">
      <form @submit.prevent="createPost">
        <label for="create-post" class="label">Leave a Note...</label>
        <input type="text" id="create-post" v-model="text" class="input" placeholder="Your Note Here">

        <button type="submit" class="button is-success is-outlined"><span class="icon is-small"><i class="fa fa-check"></i></span><span>Post it!</span></button>
      </form>
    </div>
    <hr>
    <p class="error" v-if="error">{{error}}</p>

    <div class="columns">
        <div class="column is-one-third"
             v-for="(post, index) in posts"
             :item="post"
             :index="index"
             :key="post._id"
        >
          <div class="container">
            <div class="box"
                 @dblclick="deletePost(post._id)"
            >
               <span class="created-at">{{ `${post.createdAt.getDate()}/${post.createdAt.getMonth()}/${post.createdAt.getFullYear()}`}}</span>
            <p class="text">{{post.text}}</p>
            <button class="btn-delete" @click="deletePost(post._id)"><span class="icon is-small"><i class="fa fa-times"></i></span></button>
            </div>
          </div>
        </div>
    </div>
  </div>
</template>

<script>
import PostService from "../PostService";

export default {
  name: 'PostComponent',
  data() {
    return {
      posts: [],
      error: '',
      text: ''
    }
  },
  components: {

  },
  async beforeCreate() {
    try {
      this.posts = await PostService.getPosts();
    } catch (err) {
      this.error = err.message
    }
  },
  methods: {
    async createPost() {
      await PostService.insertPost(this.text)
      this.posts = await PostService.getPosts();
      this.text = '';
    },
    async deletePost(id) {
      await PostService.deletePost(id)
      this.posts = await PostService.getPosts()
    }
  }
}
</script>

